<template>
  <div class="home-container">
    <div class="form-group">
    <h1>{{ category.name }}</h1>
  </div>
    <h4>Add Receipt</h4>
    <div class="form-group">
      Merchant: <input class="form-control" v-model="newReceiptMerchant" type="text">
    </div>
    <div class="form-group">
      Price: <input class="form-control" v-model="newReceiptPrice" type="text">
    </div>
    <div class="form-group">
      Upload: <input class="form-control" v-model="newReceiptImage" type="text">
    </div>
    <div class="form-group">
      Policy Exp Date: <input class="form-control" v-model="newReceiptPolicyExpDate" type="date">
    </div>
    <div class="form-group">
      Priority: <input class="form-control" v-model=" newReceiptPriority" type="text">
    </div>
    <button v-on:click="createReceipt()" class="btn btn-primary">Create</button>

<!--Start services area-->
<section class="services-area">
    <div class="container">
        <div class="sec-title">
            <h3>Take a Look at Your Receipts!</h3>  
        </div>
        <div class="row">
            <!--Start single service item-->
            <div v-for="receipt in category.receipts" class="col-md-4 col-sm-4 col-xs-12">
                <div class="single-service-item">
                    <div class="img-holder" style="height: 200px; width: 100%; background: black">
                        <img v-bind:src="receipt.image" style="height: 100%; width: 100%; object-fit: contain;" alt="Awesome Image">
                        <div class="overlay">
                            <div class="box">
                                <div class="content">
                                    <a class="thm-btn yellow-bg" v-on:click="setReminder(receipt)">Set Reminder</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="text-holder">
                        <div class="icon">
                            <span class="flaticon-folder"></span>    
                        </div>
                        <div class="text">
                            <h3 style="color: gold !important;">{{ receipt.merchant }}</h3>
                            <p>Price: {{ receipt.price }}</p>
                            <p>Policy Exp Date: {{ receipt.policy_exp_date }}</p>
                            <p>Priority: {{ receipt.priority }}</p>
                            <button v-on:click="destroyReceipt(receipt)">Delete</button>
                        </div>
                    </div>    
                </div>
            </div>
            <!--End single service item-->
        </div>
    </div>
</section>      
<!--End services area-->
  </div>


</template>

<style>
.home-container {
  color:Gold;
}
</style>

<script>
var axios = require("axios");


export default {
  data: function() {
    return {
      message: "Welcome to Vue.js!",
      category: {},
      newReceiptMerchant: "",
      newReceiptPrice: "",
      newReceiptImage: "",
      newReceiptPolicyExpDate: "",
      newReceiptPriority: "",
    };
  },
  created: function() {
    axios.get("http://localhost:3000/api/categories/" + this.$route.params.id).then(
      function(response) {
        console.log(response.data);
        this.category = response.data;
      }.bind(this)
    );
    axios.get("http://localhost:3000/api/receipts/").then(
      function(response) {
        console.log(response.data);
        this.receipts = response.data.merchant;
      }.bind(this)
    );
  },
  methods: {
    createReceipt: function() {
      console.log("createReceipt");
      var params = {
        input_merchant: this.newReceiptMerchant,
        input_price: this.newReceiptPrice,
        input_image: this.newReceiptImage,
        input_policy_exp_date: this.newReceiptPolicyExpDate,
        input_priority: this.newReceiptPriority,
        category_id: this.category.id
      };
      axios.post("http://localhost:3000/api/receipts", params).then(
        function(response) {
          console.log(response);
          this.category.receipts.unshift(response.data);
          this.newReceiptMerchant = "";
          this.newReceiptPrice = "";
          this.newReceiptImage = "";
          this.newReceiptPolicyExpDate = "";
          this.newReceiptPriority = "";
        }.bind(this)
      );
    },

    setReminder: function(receipt) {
      console.log("setReminder");
      axios.get("http://localhost:3000/api/sendtexts?id=" + receipt.id).then(function(response) {
        // this.category.receipts = response.data;
        console.log(response.data);
      });
      // var index = this.category.receipts.indexOf(input_receipt);
      // this.category.receipts.splice(index, 1);
    }
  },
  computed: {}
};
</script>